@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  text-align: left;
  font-size: 600;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.titlePerfil {
  font-size: 2.8em;
  font-weight: 900;
  margin: 0em auto 0.5em auto;
}

.descripcionPerfil {
  font-weight: 500;
  font-size: 1.8em;
}

#linea {
  background-color: #ffe7d4;
  height: 0.3em;
  width: 100%;
  margin: 4em 0;
}

.divSPersonal {
  margin: 2em 0;
}

.divDatos {
  margin: 3em 0;
}

.divTituloDatos {
  margin-bottom: 0.3em;
  margin-top: 1em;
}

.divDataInfo {
  margin-top: 0.3em;
  margin-bottom: 1em;
}

.dataExperienciaTexto {
  margin: 0.3em 0;
}

.dataExperienciaLinks {
  margin: 0.3em 0;
}

.dataExperienciaLinks a {
  color: #494e5f;
}

.titulosExperiencia {
  background-color: #ffe7d4;
  width: 100%;
  padding: 0.4em 0em 0.4em 0.4em;
  margin: 0.8em 0em;
}

.subtitleExperiencia {
  font-size: 1.3em;
  margin: 0.5em 0em;
}

.subsubTitleExperiencia {
  font-size: 1.1em;
  margin: 0.5em 0em;
}

.dataExperiencia {
  margin: 0.3em 0em 1em 0em;
}

.divHome {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  overflow-y: auto;
}

.divSeccionPersonal {
  color: #ffe7d4;
  background-color: #494e5f;
  padding: 3em;
  min-width: 18em;
  max-width: 30em;
  overflow-y: auto;
}

.divSeccionExperiencia {
  color: #494e5f;
  background-color: #fefefa;
  padding: 3em;
  min-width: 18em;
  max-width: 40em;
  overflow: auto;
  margin-left: 1em;
}

h3,
h2,
h4,
h1,
h5,
h6 {
  margin-block: 0;
  padding-inline: 0;
}

p {
  margin-block: 0;
  padding-inline: 0;
}

p,
li,
span {
  font-size: 1.1em;
}

li {
  list-style: none;
}

ul {
  margin-block: 0;
  padding-inline: 0;
}

a {
  text-decoration: none;
  color: #ffe7d4;
  font-size: 0.9em;
}

