h3,
h2,
h4,
h1,
h5,
h6 {
  margin-block: 0;
  padding-inline: 0;
}

p {
  margin-block: 0;
  padding-inline: 0;
}

p,
li,
span {
  font-size: 1.1em;
}

li {
  list-style: none;
}

ul {
  margin-block: 0;
  padding-inline: 0;
}

a {
  text-decoration: none;
  color: #ffe7d4;
  font-size: 0.9em;
}
